<template>
    <div>
        <input type="text" id="find" placeholder="请输入书名">
        <swipe class="my-swipe1">
            <swipe-item class="slide1"><img src="/static/img/31.jpg"/></swipe-item>
            <swipe-item class="slide2"><img src="/static/img/32.jpg"/></swipe-item>
            <swipe-item class="slide3"><img src="/static/img/33.jpg"/></swipe-item>
        </swipe>
        <div id="screen"><img src="static/img/28.png"> 搜索筛选▼ </div>
        <div>热门搜索</div>
        <div id="hot">
            <a href="">考研</a>
            <a href="">心理学</a>
            <a href="">数学</a>
            <a href="">C语言</a>
            <a href="">追风筝的人</a>
            <a href="">韩寒</a>
            <a href="">平凡的世界</a>
        </div>
        <ul id="history">
            <li><img src="static/img/27.png"/><a href="">服装设计</a><span class="remove">x</span></li>
            <li><img src="static/img/27.png"/><a href="">机械加工制造</a><span class="remove">x</span></li>
            <li><img src="static/img/27.png"/><a href="">从你的全世界路过</a><span class="remove">x</span></li>
        </ul>
        <ul>
            <li id="remove">清除搜索历史</li>
        </ul>






    </div>
</template>

<script>
import "vue-swipe/dist/vue-swipe.css";
import { Swipe, SwipeItem } from "vue-swipe";
export default {
    components: {
        'swipe': Swipe,
        'swipe-item': SwipeItem,
    },
    mounted() {
        var remove = document.getElementById("remove");
        var history = document.getElementById("history");
        remove.onclick=function(){
            history.style.display="none";
            remove.style.display="none";
        }
    }
}
</script>

<style scopend>
    .my-swipe1 {
  height: 200px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.slide1 {
  color: #fff;
  background-size: 100%;
}

.slide2 {
  color: #000;
}

.slide3 {
  color: #fff;
  background-size: 100%;
}
    .my-swipe1 img{
        height: 3rem;width: 100%;
    }
    #find{
        margin-left: 1.5%;
        height: 0.5rem;
        width: 95%;
        background: #CCCCCC;
        border-radius: 5% 5%;
        text-align: center;
    }
    
    #screen{
        text-align: center;
        width: 100%;
        height: 1rem;
        background: white;
        line-height: 1rem;
        font-size: 18px;
        border-bottom: 1px solid #F6F6F6;
    }
    #screen img{
        height: 1rem;
    }
    #history li{
        width: 100%;
        height: 0.7rem;
        line-height: 0.7rem;
        background: white;
    }
    #history li span{
        float:right;
        position: relative;
        right: 4%;
    }
    #history img{
        height: 0.7rem;
    }
    #history li a{
        color: gray;
        border-bottom: 1px solid #F6F6F6;
    }
    #hot a{
        color: black;
        padding: 4%;
    }
    #remove {
        text-align: center;
        color: #6FACE1;
    }
</style>



