<template>
<div>
    <swipe class="my-swipe3">
            <swipe-item class="slide1"><img src="/static/img/13.png"/></swipe-item>
            <swipe-item class="slide2"><img src="/static/img/14.png"/></swipe-item>
            <swipe-item class="slide3"><img src="/static/img/15.png"/></swipe-item>
        </swipe>
    <div id="meun">
        <div class="meun" v-for="tt in homepagelist" :key="tt.id" >
            <img :src="tt.pic" />
            <p>{{tt.txt}}</p>
        </div>
        
    </div>
    <div id="list">
        <ul>
            <li v-for="aa in homepagelist1" :key="aa.id">
                <img :src="aa.pic" />
                {{aa.txt}}
                </li>
        </ul>
    </div>
    <div id="finish"></div>
    
</div>    
</template>

<script>
import Axios from "axios"
import "vue-swipe/dist/vue-swipe.css";
import { Swipe, SwipeItem } from "vue-swipe";
export default {
    components: {
        'swipe': Swipe,
        'swipe-item': SwipeItem,
    },
    data(){
        return{
            homepagelist:[],
            homepagelist1:[],
        }
    },
    mounted(){

    
          
        let url = "./static/data/homepageimg.json"
        Axios.get(url).then((res)=>{
            this.homepagelist=res.data.data.meun;
        }).catch() 

        let url1 = "./static/data/homepageimg2.json"
        Axios.get(url1).then((res)=>{
            this.homepagelist1=res.data.data.meun;
        }).catch() 

    }

    
    
}
</script>

<style scopend>
.my-swipe3 img{
        height: 3rem;width: 100%;
    }
.my-swipe3 {
  height: 200px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.slide1 {
  color: #fff;
  background-size: 100%;
}

.slide2 {
  color: #000;
}

.slide3 {
  color: #fff;
  background-size: 100%;
}
body{background: #EEEEEE}
    #meun{
        width: 100%;
        margin-bottom: 2vw;
        background: white;
        height: 50vw;
        text-align: center;
        }
    #meun div{width: 30%;
        height: 25vw;
        float: left;
        margin: 1%;
    }
    .meun img{width: 50%;}
    #list{
        margin-top: 5vw;
        background: white;
        width: 95%;
        padding-left: 4%;
    }
    #list li{
        background: white;
        
        height: 1rem;
        line-height: 1rem;
        font-size: 15px;
    }
    #list img{
        margin-top: 2vw;
    }
    #finish{
        width: 100%;
        height: 1rem;
    }
    
</style>



